import React, {Component} from 'react';
import {Route, Switch} from 'react-router-dom';

import './main.scss';
import {Icon} from 'antd';
import Homepage from './home';

class Mainpage extends Component{
  constructor(props){
    super(props);
  }

  render(){
    return (
      <div>
        <header className="cscec-pl-header">
          <a href="/" className="cscec-pl-nbbrand">总承包公司大数据中心</a>
          <div className="cscec-pl-nbcontent">
            <div></div>
            <ul className="cscec-pl-nbgroup">
            <li className="cscec-pl-nbitem"><a>项目管理系统</a></li>
            <li className="cscec-pl-nbitem"><a>人力资源系统</a></li>
              <li className="cscec-pl-nbitem" style={{display: 'flex',alignItems: 'center'}}>
                <span className="user-img" style={{display: 'inline-block',float: 'left'}}>
                  <img src={require('../../assets/images/icon_user_default.png')} alt="头像"></img>
                </span>
                <span id="idspan_user">张三</span>
                {/* <Icon type="mail" style={{fontSize: 14, paddingLeft: 5}} /> */}
              </li>
              <li className="cscec-pl-nbitem"><a>退出</a></li>
            </ul>
          </div>
        </header>
        <aside className="cscec-pl-sidebar">
          <div style={{marginTop: '64px'}}>
            <ul>
              <li>
                <a className="sidebar-row" href="#">
                  <Icon type="home" className="sidebar-icon" /><span>首页</span>
                </a>
              </li>
              <li>
                <a className="sidebar-row" href="#">
                  <Icon type="setting" className="sidebar-icon" /><span>个人设置</span>
                </a>
              </li>
            </ul>
          </div>
        </aside>
        <section className="cscec-pl-section" style={{marginLeft: '50px',marginTop: '64px',padding: '30px'}}>
          <div className="cscec-pl-content">
            <Switch>
              <Route exact path="/home" component={Homepage} />
              <Route exact path="/" component={Homepage} />
            </Switch>
          </div>
        </section>
      </div>
    );
  }
}

export default Mainpage;