import axios from 'axios';
// import qs from 'qs';
import {Modal, message} from 'antd';
const confirm = Modal.confirm;

// 全局api定义
let BaseUrl = '';
if(process.env.NODE_ENV === 'development'){
  BaseUrl = '/api';
}

// 获取sessionStorage信息
let commonMethod={
  getToken: ()=>{
    if(sessionStorage.getItem('auth-token')){
      return sessionStorage.getItem('auth-token');
    }
    return null;
  }
}


// 向后台发送请求封装
const axios_PL = axios.create({
  timeout: 10000,
  withCredentials: true
});
// axios_PL.interceptors.response.use(response => {
//   return response;
// }, error=>{
//   return Promise.reject(error);
// });

let http={
  post:null,
  get:null,
  redirectTo_blank:null
}
// 添加请求拦截器，响应拦截器，？？？有api_token的请求
http.post=function(url, paramsobj={}){
  // qs序列化的作用？？?
  return axios_PL.post(url, paramsobj)
    .then((res)=>res.data)
    .catch(function(error){
      if(error.request){
        if(error.request.status==404){
          let respUrl=error.request.responseURL;
          let idx=respUrl.indexOf('/Account/Login')
          if(idx>0){
            let hostUrl=respUrl.substring(0, idx);
            location.href=hostUrl+'/#/';
            return false;
          }
          return {Result: 'error', Data: '请求地址错误'}
        }
        return {Result: 'error', Data: '其他错误'};
      }
    });
}

http.get=function(url, paramsobj){
  return axios_PL.get(url, {params: paramsobj})
    .then((res)=>res.data)
    .catch(function(error){
      if(error.request){
        if(error.request.status==0&&error.code==='ECONNABORTED'){
          return {Result: 'error', Data: '请求时间超过5s，请检查网络'};
        }
        if(error.request.status==404){
          let respUrl=error.request.responseURL;
          let idx=respUrl.indexOf('/Account/Login')
          if(idx>0){
            let hostUrl=respUrl.substring(0, idx);
            location.href=hostUrl+'/#/';
            return false;
          }
          return {Result: 'error', Data: '请求地址错误'}
        }
        return {Result: 'error', Data: '其他错误'};
      }
    });
}

http.redirectTo_blank=function(url){
  if(url) window.open(url+'?token='+commonMethod.getToken(), '_blank');
}


// 默认提示框
let cscec3bMsg={
  success: null,
  error: null,
  warning: null,
  confirm: null
}
cscec3bMsg.success=(msg, second=3)=>message.success(msg, second);
cscec3bMsg.error=(msg, second=3)=>message.error(msg, second);
cscec3bMsg.warning=(msg, second=3)=>message.warning(msg, second);
cscec3bMsg.confirm=(title, content)=>{
  return new Promise((resolve,)=>{
    confirm({
      title: title,
      content: content,
      okText: '确认',
      cancelText: '取消',
      onOk() {
        resolve('ok');
      },
      onCancel() {
        resolve('cancel');
      }
    });
  });
}

export {BaseUrl, http, cscec3bMsg}

