import React, { Component } from 'react';
import PropTypes  from 'prop-types';
import '../assets/css/component.scss';
import {http} from '../utils/Methodutils';

class Jcpanel extends Component{
  constructor(props){
    super(props);
  }
  componentDidMount(){}
  getBoxtitleicon(){
    const {icontype, extra: src} = this.props;
    if(icontype==='img'){
      let bgimg=`url(${process.env.PUBLIC_URL+src})`;
      return (<div className="box-titleicon" style={{backgroundImage: bgimg}}></div>);
    }
    if(icontype==='icon'){
      return (
        <svg className="icon" aria-hidden="true">
          <use xlinkHref={src}></use>
        </svg>
      )
    }
    return null;
  }
  clickMore(link){
    if(link) http.redirectTo_blank(`http://${link}`);
  }
  render(){
    const {title, className: bgclass, morelink} = this.props;
    return (
      <div className={`jc-plbox ${bgclass}`}>
        <div className="box-b">
          <div className="box-head">
            <div className="box-l">
              {this.getBoxtitleicon()}
              <span className="box-title">{title}</span>
            </div>
            <a className="more-link" href="#" onClick={()=>this.clickMore(morelink)}>更多&gt;&gt;</a>
          </div>
          <div className="box-con mt10">
            <div className="el-tabs">
              <div className="el-panel">
                {this.props.children}
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

Jcpanel.defaultTypes={
}
Jcpanel.propTypes={
  title: PropTypes.string.isRequired,
  icontype: PropTypes.oneOf(['icon', 'img']),
  extra: PropTypes.string,
  morelink: PropTypes.string
}
export default Jcpanel;