import React, {Component} from 'react';
import './main.scss';
// import {Icon} from 'antd';
import Slidingmenu from '@/components/slidingmenu';
import Jcpanel from '@/components/Jcpanel';
import {http} from '@/utils/Methodutils';
import Url from '@/utils/Url';

class Homepage extends Component{
  constructor(props){
    super(props);
  }
  componentDidMount(){
    this.GetAuthenticToken();
  }
  iframeref=null;
  async GetAuthenticToken(){
    let ret = await http.post(Url.GetAuthenticToken, {});
    if(ret.Result==='ok'){
      sessionStorage.setItem('auth-token', ret.Data);
      this.iframeref.src='http://localhost:55555/ZcbAuthentic/RedirectToHrStatisticPartial?token='+ret.Data;
    }
  }
  render(){
    return (
      <div>
        <Slidingmenu></Slidingmenu>
        <div className="cscec-pl-jccontainer">
          <div className="cscec-pl-row mt-20 clearfix">
            <Jcpanel
                className="sm-plcol-12 lg-plcol-6"
                title="人力资源"
                icontype="img"
                extra="static/img/人力资源.ico"
                morelink="localhost:55555/ZcbAuthentic/RedirectToHrStatistic"
            >
              <iframe style={{border:0, width:'100%', height:250}} ref={(ifr)=>this.iframeref=ifr} />
            </Jcpanel>
            <Jcpanel
                className="sm-plcol-12 lg-plcol-6"
                title="进度"
                icontype="img"
                extra="static/img/进度管理.png"
                //extra="#icon-jindu"
            >
              <p>123</p>
            </Jcpanel>
          </div>
          <div className="cscec-pl-row mt-20 clearfix">
            <Jcpanel
                className="sm-plcol-12 lg-plcol-6"
                title="安全"
                icontype="img"
                extra="static/img/平安总包.png"
            >
              <p>123</p>
            </Jcpanel>
            <Jcpanel
                className="sm-plcol-12 lg-plcol-6"
                title="资金"
            >
              <p>123</p>
            </Jcpanel>
          </div>
          <div className="cscec-pl-row mt-20 clearfix">
            <Jcpanel
                className="sm-plcol-12 lg-plcol-6"
                title="成本"
            ></Jcpanel>
            <Jcpanel
                className="sm-plcol-12 lg-plcol-6"
                title="分供方"
            >
              <p>123</p>
            </Jcpanel>
          </div>
        </div>
      </div>
    );
  }
}

export default Homepage;