import React from 'react';
import {HashRouter as Brouter, Switch, Route} from 'react-router-dom';

import Mainpage from '@/pages/main/main';

export default ()=>{
  return (
    <Brouter>
      <Switch>
        {/* <Route exact path="/main" component={Mainpage} /> */}
        <Route path="/" component={Mainpage} />
      </Switch>
    </Brouter>
  );
}