import React, {Component} from 'react';
import '../assets/css/component.scss';
import _ from 'lodash';
import {http} from '../utils/Methodutils';

const gnkuais=[{
  key: 'gnl-1',
  showtext: '九宫格',
  islink: false,
  alertnum: 7,
  bgcss: 'bg-orange',
  icon: '#icon-icon-test',
  link: 'localhost:55555/ZcbAuthentic/RedirectToStaistic'
},{
  key: 'gnl-2',
  showtext: '运营态势',
  islink: false,
  alertnum: 0,
  bgcss: 'bg-deep-blue',
  icon: '#icon-yunying'
},{
  key: 'gnl-3',
  showtext: '知识库',
  islink: false,
  alertnum: 2,
  bgcss: 'bg-green',
  icon: '#icon-zhishiku',
  link: 'localhost:55555/ZcbAuthentic/RedirectToRepo'
},{
  key: 'gnl-4',
  showtext: '系统消息',
  islink: false,
  alertnum: 1,
  bgcss: 'bg-red',
  icon: ''
},{
  key: 'gnl-5',
  showtext: '帮助中心',
  islink: false,
  alertnum: 7,
  bgcss: 'bg-green',
  icon: ''
},{
  key: 'gnl-6',
  showtext: '我的设置',
  islink: false,
  alertnum: 0,
  bgcss: 'bg-green',
  icon: ''
}];
const gnkwidth = 145; // 实际宽度125+margin-right:20
// const gnktotalwidth=145*gnkuais.length-20; // 功能块滑动父div实际宽度，最后一个功能块的margin-left:0

export default class Slidingmenu extends Component{
  constructor(props){
    super(props);
    this.state={
      left_arrow_visible: true,
      pianyi_x: 0
    }
  }
  componentDidMount(){
    this.gnkvisiblewidth = this.sliderwrapper.width||this.sliderwrapper.clientWidth;
    //if(gnkvisiblewidth<gnktotalwidth){}
    if(window.addEventListener){
      //window.addEventListener('resize', ()=>this.browserScreenResize())
      window.addEventListener('resize', _.throttle(()=>this.browserScreenResize(), 100))
    }
  }
  componentWillUnmount(){
    if(window.removeEventListener){
      window.removeEventListener('resize', ()=>this.browserScreenResize());
    }
  }
  // 滑动的el dom阶段，ref获取
  sliderwrapper=null;
  dangqian_gnk=0;
  // 滑动可视窗口的宽度
  gnkvisiblewidth=0
  browserScreenResize(){
    this.gnkvisiblewidth = this.sliderwrapper.width||this.sliderwrapper.clientWidth;
  }
  routertoBs(link){
    //if(link) window.open(`http://${link}`, '_blank');
    if(link) http.redirectTo_blank(`http://${link}`);
  }
  getGongnengkuais(){
    return gnkuais.map(d=>{
      let bgcssstr = `slide-box ${d.bgcss}`;
      return (
        <div className={bgcssstr} key={d.key} onClick={()=>this.routertoBs(d.link)}>
          <div className="box-sl">
            <p className="fz16" title={d.showtext}>{d.showtext}</p>
            <p className="fz32" style={{visibility: 'hidden'}}>{d.alertnum}</p>
          </div>
          <div className="box-sr">
            <svg className="icon" aria-hidden="true">
              <use xlinkHref={d.icon}></use>
            </svg>
          </div>
        </div>
      );
    })
  }
  arrowLeftclick(){
    let shownums = Math.ceil(this.gnkvisiblewidth/gnkwidth);
    if(shownums>(gnkuais.length-this.dangqian_gnk)){
      this.setState({pianyi_x: -(this.dangqian_gnk*gnkwidth+gnkwidth*2/3)});
      setTimeout(() => {
        this.setState({pianyi_x: -(this.dangqian_gnk*gnkwidth)});
      }, 500);
      return;
    }
    this.dangqian_gnk++;
    this.setState({
      pianyi_x: (-this.dangqian_gnk*gnkwidth)
    });
  }
  arrowRightclick(){
    if(this.dangqian_gnk===0){
      this.setState({pianyi_x: gnkwidth*2/3});
      setTimeout(() => {
        this.setState({pianyi_x: 0});
      }, 500);
      return;
    }
    this.dangqian_gnk--;
    this.setState({
      pianyi_x: (-this.dangqian_gnk*gnkwidth)
    });
  }
  render(){
    const {left_arrow_visible, pianyi_x}= this.state;
    let pianyijuli = `translate(${pianyi_x}px)`;
    return (
      <div className="slide-container clearfix">
        <table className="slide-table">
          <thead>
            <tr>
              <th className={'arrow-th '+(left_arrow_visible?'':'hide')}>
                <div className="arrow-wrapper" onClick={()=>this.arrowLeftclick()}>
                  <img className="img-prev" src={require('../assets/images/prev-arrow.png')}></img>
                </div>
              </th>
              <th className="slide-th" ref={el=>this.sliderwrapper=el}>
                <div className="slide-quyu">
                  <div className="slide-box-group" style={{transform: pianyijuli}}>
                    {this.getGongnengkuais()}
                  </div>
                </div>
                {/* <table className="slide-table-gnk">
                  <tbody>
                    <tr>
                      <td className="slide-td-gnd">123</td>
                      <td className="slide-td-gnd">444</td>
                      <td className="slide-td-gnd">666</td>
                    </tr>
                  </tbody>
                </table> */}
              </th>
              <th className="arrow-th" onClick={()=>this.arrowRightclick()}>
                <div className="arrow-wrapper">
                  <img className="img-next" src={require('../assets/images/next-arrow.png')}></img>
                </div>
              </th>
            </tr>
          </thead>
        </table>
      </div>
    );
  }
}
